.news-block{
	width: 100%;
	display: table;
	margin-bottom: 18px;
	@media (max-width: 991px){
		margin-bottom: 20px;
	}
	@media (max-width: 512px){
		margin-bottom: 15px;
	}
	@nest & img{
		max-width: 195px;
		height: 195px;
		object-fit: cover;
		border-radius: 10px;
		overflow: hidden;
		display: table-cell;
		vertical-align: middle;
		@media (max-width: 512px){
			display: block;
			width: 100%;
			max-width: 320px;
			height: 180px;
		}
	}
	@nest &__text{
		display: table-cell;
		padding: 7px 0 9px 13px;
		vertical-align: middle;
		@media (max-width: 991px){
			padding: 6px 0 0px 13px;
			vertical-align: top;
		}
		@media (max-width: 512px){
			display: block;
			padding-left: 0;
			padding-top: 11px;
		}
	}
	@nest & a{
		font-size: 21px;
		color: #d550d0;
		display: block;
		font-weight: 300;
		margin: 0px 0 11px;
		line-height: normal;
		text-decoration: none;
		transition: color 300ms;
		@nest &:hover{
			color: #9e30a0;
		}
		@media (max-width: 991px){
			margin: 0 0 9px;
			line-height: 25px;
		}
	}
	@nest & p{
		font-size: 16px;
		color: #000;
		margin-bottom: 14px;
		line-height: 19px;
		letter-spacing: -.1px;
		word-spacing: -.35px;
		@media (max-width: 991px){
			line-height: 20px;
			max-height: 56px;
			overflow: hidden;
			margin-bottom: 13px;
		}
	}
	@nest &__hashtag{
		font-size: 16px;
		color: #000;
		margin-bottom: 12px;
		@media (max-width: 991px){
			margin-bottom: 10px;
		}
		@media (max-width: 512px){
			display: none;
		}
		@nest & a{
			font-size: 16px;
			color: #a1a1a1;
			margin: 0;
			padding-right: 3px;
			display: inline-block;
			text-decoration: none;
			transition: color 300ms;
			@nest &:hover{
				color: var(--purple);
			}
		}
	}
	@nest &__bottom{
		font-size: 16px;
		color: #000;
		@nest & time{
			letter-spacing: -.3px;
			@media (max-width: 512px){
				display: block;
				margin-bottom: 5px;
			}
		}
		@nest & span{
			color: #a1a1a1;
			margin-left: 10px;
			padding-left: 20px;
			display: inline-block;
			position: relative;
			@nest &:nth-child(2){
				padding-left: 29px;
			}
			@media (max-width: 512px){
				margin-left: 0;
				margin-right: 10px;
			}
			@nest & svg{
				position: absolute;
				content: '';
				left: 0;
				top: 50%;
				height: 13px;
				margin-top: -6.5px;
			}
		}
	}
}