.modal{
	display: none;
  @nest &--show{
    display: block;
  }
}

.modal{
  padding: 0 30px 40px;
}
.modal--order{
  width: 400px;
}
.modal--master{
  width: 520px;
}
.fancybox-opened .fancybox-skin{
  box-shadow: 0px 7px 18px 0px rgba(0, 0, 0, 0.3);
}
.fancybox-skin{
  border-radius: 10px;
}
.master-modal .fancybox-close{
  display: none;
}
.modal--master span{
  margin-top: 30px;
  text-align: center;
  font-size: 24px;
  font-weight: 300;
  display: block;
}
.modal__top{
  margin-top: 30px;
  font-size: 0;
  padding-bottom: 20px;
  border-bottom: solid 1px #e8e8e8;
}
.modal__top .modal__top-left{
  width: 60%;
  padding: 0 5px;
  display: inline-block;
  vertical-align: top;
}
.modal__top .modal__top-right{
  width: 40%;
  padding:  0 5px;
  text-align: center;
  display: inline-block;
  vertical-align: top;
}
.modal__top .modal__top-right i{
  font-size: 14px;
  font-weight: 100;
  color: #bababa;
  margin-top: 13px;
  text-align: center;
}
.modal__top i{
  font-size: 14px;
  font-weight: 100;
  color: #bababa;
  margin-top: 14px;
  font-style: normal;
  display: inline-block;
}
.modal form .modal__input{
  width: 100%;
  background-color: #fff;
  border: 1px solid #dddddd;
  padding: 10px 18px;
  margin: 10px 0;
  outline: none;
  color: #000;
  font-size: 14px;
  font-weight: 300;
  border-radius: 10px;
}
.modal form .modal__input:focus{
  color: #000;
}
.modal form .modal__input:focus, .modal form .modal__input:hover{
  border: 1px solid #eaa7e7;
}
.modal form .modal__input--xl{
  text-align: center;
  font-size: 16px;
  margin: 0;
  padding: 14px 18px 14px;
}
.modal form .modal__input--md{
  padding: 17px 18px 16px;
}
.modal form .modal__input--md{
  padding: 13px 18px 11px;
}
.modal form .modal__btn{
  width: 100%;
  padding: 14px 0 13px;
  color: #fff;
  border-style: solid;
  border-width: 1px;
  border-color: rgb(181, 88, 179);
  border-radius: 9px;
  background: -moz-linear-gradient( 90deg, rgb(158,48,160) 0%, rgb(213,80,208) 100%);
  background: -webkit-linear-gradient( 90deg, rgb(158,48,160) 0%, rgb(213,80,208) 100%);
  background: -ms-linear-gradient( 90deg, rgb(158,48,160) 0%, rgb(213,80,208) 100%);
  box-shadow: 0px 2px 7px 0px rgba(213, 80, 208, 0.6);
  font-size: 18px;
  transition: background 300ms;
}
.modal form .modal__btn:hover{
  background: -moz-linear-gradient( 90deg, rgb(101,31,102) 0%, rgb(173,65,169) 100%);
  background: -webkit-linear-gradient( 90deg, rgb(101,31,102) 0%, rgb(173,65,169) 100%);
  background: -ms-linear-gradient( 90deg, rgb(101,31,102) 0%, rgb(173,65,169) 100%);
}
.modal__bottom {
  font-size: 0;
}
.modal__bottom p{
  font-size: 16px;
  font-weight: 300;
  text-align: center;
  margin-top: 24px;
  color: #4f4f4f;
}
.modal__bottom p i{
  color: #bababa;
  font-size: 16px;
  font-style: normal;
}
.modal__bottom .modal__bottom-item{
  width: 50%;
  display: inline-block;
  padding: 0 5px;
}
.modal form .modal__msg{
  width: 100%;
  background-color: #fff;
  height: 80px;
  border: 1px solid #dddddd;
  padding: 10px 18px;
  margin: 10px 0;
  outline: none;
  color: #000;
  font-size: 14px;
  font-weight: 300;
  border-radius: 10px;
}

.modal form .modal__msg:focus, .modal form .modal__msg:hover{
  border: 1px solid #eaa7e7;
}
.select{ 
  width: 100%; 
  position: relative; 
  max-width: 197px; 
  display: inline-block; 
  margin: 10px 0;
  vertical-align: middle; 
}
.select:after{ 
  position: absolute; 
  content: ''; 
  right: 12px; 
  top: 50%; 
  border-right: 4px solid transparent; 
  border-left: 4px solid transparent; 
  border-top: 8px solid #000; 
  transform: translateY(-50%); 
} 
.select select{ 
  font-size: 14px; 
  color: #000; 
  height: 33px; 
  width: 100%; 
  outline: none; 
  cursor: pointer; 
  border-radius: 5px; 
  padding-left: 17px; 
  -webkit-appearance: none; 
  -ms-appearance: none; 
  -moz-appearance: none; 
  border: 1px solid #f0f0f0; 
  background-image: -moz-linear-gradient( 90deg, rgb(229,229,229) 0%, rgb(254,254,254) 100%); 
  background-image: -webkit-linear-gradient( 90deg, rgb(229,229,229) 0%, rgb(254,254,254) 100%); 
  background-image: -ms-linear-gradient( 90deg, rgb(229,229,229) 0%, rgb(254,254,254) 100%); 
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.15); 
}
.modal__select{
  max-width: 300px;
}
.modal__select select{
  height: 40px;
}
.modal form .modal__input::-webkit-input-placeholder {color:#bababa; font-size: 14px; font-weight: 300;}
.modal form .modal__input::-moz-placeholder          {color:#bababa; font-size: 14px; font-weight: 300;}/* Firefox 19+ */
.modal form .modal__input:-moz-placeholder           {color:#bababa; font-size: 14px; font-weight: 300;}/* Firefox 18- */
.modal form .modal__input:-ms-input-placeholder      {color:#bababa; font-size: 14px; font-weight: 300;}
.modal form .modal__input--xl::-webkit-input-placeholder {color:#000; font-size: 16px; font-weight: 300;}
.modal form .modal__input--xl::-moz-placeholder          {color:#000; font-size: 16px; font-weight: 300;}/* Firefox 19+ */
.modal form .modal__input--xl:-moz-placeholder           {color:#000; font-size: 16px; font-weight: 300;}/* Firefox 18- */
.modal form .modal__input--xl:-ms-input-placeholder      {color:#000; font-size: 16px; font-weight: 300;}
.modal--order span{
  padding: 18px 0 19px 40px;
  font-size: 22px;
  font-weight: 300;
  display: block;
  width: 100%;
  background-color: rgb(213, 80, 208);
  color: #fff;
}
.fancybox-skin{
  overflow: hidden;
}
.modal--order{
  padding: 0 0 40px;
}
.wrap-form{
  padding: 20px 35px 0px;
}
.modal__msg--order{

}
.modal form .modal__msg--order::-webkit-input-placeholder {color:#000; font-size: 16px; font-weight: 300;}
.modal form .modal__msg--order::-moz-placeholder          {color:#000; font-size: 16px; font-weight: 300;}/* Firefox 19+ */
.modal form .modal__msg--order:-moz-placeholder           {color:#000; font-size: 16px; font-weight: 300;}/* Firefox 18- */
.modal form .modal__msg--order:-ms-input-placeholder      {color:#000; font-size: 16px; font-weight: 300;}
.hidden{
  display: none;
}
.wrap-order-modal .fancybox-close {
  position: absolute;
  top: 17px;
  right: 17px;
  width: 28px;
  background-image: url(../img/close.svg);
  height: 28px;
  cursor: pointer;
  z-index: 8040;
}
.wrap-master-modal .fancybox-close{
  background-image: url(../img/close-master.svg);
  width: 16px;
  height: 16px;
  right: 10px;
  top: 10px;
}
.fancybox-overlay {
  -webkit-transition: opacity 300ms; 
  transition: opacity 300ms; 
  background: #ffffff; 
  background: rgba(255, 255, 255, 0.9); 
}
 
.modal-block button{
  position: static;
}
.modal-block span{
  padding: 18px 0 19px 40px;
  font-size: 22px;
  font-weight: 300;
  display: block;
  width: 100%;
  background-color: rgb(213, 80, 208);
  color: #fff;
}
.modal-block .modal{
  padding: 0 0 15px;
}