.fixed-list{
	padding: 0;
	margin: 4px 0 0;
	list-style: none;
	display: inline-block;
	@nest &__item{
		margin-right: 56px;
		display: inline-block;
		vertical-align: middle;
		@nest &:last-child{
			margin-right: 0;
		}
		@media (max-width: 1199px){
			margin-right: 25px;
		}
		@media (max-width: 991px){
			margin-right: 41px;
		}
		@media (max-width: 767px){
			margin-right: 15px;
		}
	}
	@nest &__link{
		font-size: 16px;
		color: #fff;
		text-decoration: none;
		transition: color 300ms;
		@media (max-width: 991px){
			font-size: 15px;
		}
		@nest &:hover{
			color: var(--purple);
		}
	}
}