.main-slider{
	margin: 20px 0 0;
	@media (max-width: 512px){
		margin-top: 16px;
	}
	@media (max-width: 512px){
		background: #f4f4f4;
	}
	@nest &__item{
		min-height: 330px;
		outline: none;
		background: #f4f4f4;
		position: relative;
		padding: 45px 0 0 75px;
		@media (max-width: 991px){
			padding: 49px 0 0 50px;
		}
		@media (max-width: 767px){
			padding: 25px 0;
		}
		@media (max-width: 512px){
			padding: 16px 0 10px;
			min-height: 0;
		}
		@nest & img{
			position: absolute;
			top: 16px;
			right: 81px;
			@media (max-width: 1199px){
				top: 15px;
				right: 15px;
				max-height: 289px;
			}
			@media (max-width: 767px){
				position: static;
				width: 60%;
				margin: auto;
				margin-bottom: -16px;
			}
			@media (max-width: 512px){
				margin-bottom: -22px;
			}
		}
		@nest & h1{
			font-size: 40px;
			color: #000;
			margin: 0 0 35px;
			max-width: 500px;
			font-weight: 300;
			@media (max-width: 991px){
				font-size: 32px;
				margin-bottom: 62px;
				line-height: 37px;
			}
			@media (max-width: 767px){
				text-align: center;
				margin-bottom: 30px;
			}
			@media (max-width: 512px){
				font-size: 20px;
				line-height: normal;
				margin-bottom: 12px;
			}
		}
		@nest & .btn-modal{
			display: block;
			margin-bottom: 10px;
			@media (max-width: 767px){
				z-index: 10;
				margin: 0 auto 10px;
				position: relative;
			}
			@media (max-width: 512px){
				width: 100%;
				margin-bottom: 7px;
			}
		}
	}
	@nest & .slick-arrow{
		position: absolute;
		content: '';
		top: 50%;
		width: 35px;
		height: 85px;
		border: 0;
		padding: 0;
		font-size: 0;
		z-index: 20;
		outline: none;
		margin-top: -42.5px;
		background: transparent;
		background: color(#000 alpha(7%));
		transition: background 300ms;
		@media (max-width: 512px){
			width: 27px;
			height: 66px;
		}
		@nest &:hover{
			background: color(#000 alpha(20%));
			@nest &:after{
				opacity: 1;
			}
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 50%;
			top: 50%;
			opacity: .7;
			transition: opacity 300ms;
			transform: translate(-50%, -50%);
		}
	}
	@nest & .slick-prev{
		left: 0;
		border-radius: 0 9px 9px 0;
		@media (max-width: 512px){
			left: -15px;
		}
		@nest &:after{
			background: resolve('prev-arrow.svg');
			width: width('prev-arrow.svg');
			height: height('prev-arrow.svg');
		}
	}
	@nest & .slick-next{
		right: 0;
		border-radius: 9px 0 0 9px;
		@media (max-width: 512px){
			right: -15px;
		}
		@nest &:after{
			background: resolve('next-arrow.svg');
			width: width('next-arrow.svg');
			height: height('next-arrow.svg');
		}
	}
	@nest & .slick-dots{
		position: absolute;
		content: '';
		left: 50%;
		bottom: 20px;
		padding: 0;
		margin: 0;
		font-size: 0;
		list-style: none;
		transform: translateX(-50%);
		@media (max-width: 767px){
			bottom: 15px;
		}
		@nest & li{
			margin: 0 5px;
			display: inline-block;
			vertical-align: middle;
			@nest &.slick-active{
				@nest & button{
					background: color(#000 alpha(40%));
				}
			}
			@nest & button{
				width: 35px;
				height: 3px;
				border: 0;
				padding: 0;
				outline: none;
				background: color(#000 alpha(10%));
			}
		}
	}
}