.sidebar-products{
	margin: 0 -10px;
	@nest & .slick-current{

	}
	@nest & .slick-arrow{
		position: absolute;
		content: '';
		top: 50%;
		width: 35px;
		height: 85px;
		border: 0;
		padding: 0;
		font-size: 0;
		z-index: 20;
		outline: none;
		margin-top: -42.5px;
		background: transparent;
		background: color(#000 alpha(7%));
		transition: background 300ms;
		@nest &:hover{
			background: color(#000 alpha(20%));
			@nest &:after{
				opacity: 1;
			}
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 50%;
			top: 50%;
			opacity: .7;
			transition: opacity 300ms;
			transform: translate(-50%, -50%);
		}
	}
	@nest & .slick-prev{
		left: 10px;
		border-radius: 0 9px 9px 0;
		@nest &:after{
			background: resolve('prev-arrow.svg');
			width: width('prev-arrow.svg');
			height: height('prev-arrow.svg');
		}
	}
	@nest & .slick-next{
		right: 10px;
		border-radius: 9px 0 0 9px;
		@nest &:after{
			background: resolve('next-arrow.svg');
			width: width('next-arrow.svg');
			height: height('next-arrow.svg');
		}
	}
}