.service-info{
	width: 100%;
	display: table;
	margin-bottom: 29px;
	@media (max-width: 991px){
		margin-bottom: 13px;
	}
	@media (max-width: 512px){
		margin-bottom: 17px;
	}
	@nest & img{
		display: table-cell;
		vertical-align: top;
		@media (max-width: 512px){
			display: block;
			width: 100%;
		}
	}
	@nest & h2{
		font-size: 24px;
		color: #000;
		font-weight: 300;
		margin-bottom: 10px;
		@media (max-width: 512px){
			font-size: 22px;
		}
	}
	@nest & p{
		font-size: 16px;
		color: #000;
		margin: 0;
		line-height: 24px;
		@media (max-width: 991px){
			line-height: 20px;
		}
		@media (max-width: 512px){
			margin-top: 8px;
			line-height: 19px;
		}
	}
	@nest &__text{
		max-width: 540px;
		padding-left: 15px;
		display: table-cell;
		vertical-align: top;
		@media (max-width: 991px){
			max-width: 440px;
		}
		@media (max-width: 512px){
			display: block;
			padding-left: 0;
		}
	}
}