.bot-line{
	position: fixed;
	content: '';
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 100;
	padding-top: 5px;
	background: #000;
	@media (max-width: 991px){
		display: none;
	}
	@nest & .container{
		position: relative;
	}
	@nest &__hide{
		position: absolute;
		content: '';
		left: -97px;
		top: -26px;
		font-size: 0;
		cursor: pointer;
		transition: all 300ms;
		@media (max-width: 1380px){
			left: -20px;
		}
		@nest &-rotate{
			top: -31px;
			transform: rotate(180deg);
		}
	}
	@nest &__inner{
		padding: 4px 0 9px;
		display: none;
	}
	@nest &__order{
		font-size: 14px;
		color: #000;
		width: 151px;
		margin-right: 6px;
		min-height: 32px;
		line-height: 32px;
		text-align: center;
		border-radius: 16px;
		display: inline-block;
		text-decoration: none;
		box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.15);
		background-image: -moz-linear-gradient( 90deg, rgb(229,229,229) 0%, rgb(254,254,254) 100%);
		background-image: -webkit-linear-gradient( 90deg, rgb(229,229,229) 0%, rgb(254,254,254) 100%);
		background-image: -ms-linear-gradient( 90deg, rgb(229,229,229) 0%, rgb(254,254,254) 100%);
		@nest &:hover{
			box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.15);
			background-image: -moz-linear-gradient( 90deg, rgb(182,182,182) 0%, rgb(251,251,251) 100%);
			background-image: -webkit-linear-gradient( 90deg, rgb(182,182,182) 0%, rgb(251,251,251) 100%);
			background-image: -ms-linear-gradient( 90deg, rgb(182,182,182) 0%, rgb(251,251,251) 100%);
		}
		@nest &:nth-child(3){
			margin-right: 33px;
			@media (max-width: 1199px){
				margin-right: 6px
			}
		}
	}
	@nest &__func{
		font-size: 14px;
		color: #fff;
		font-weight: 100;
		margin: 0 14px 0 0;
		position: relative;
		line-height: 20px;
		padding: 0 0 0 25px;
		display: inline-block;
		text-decoration: none;
		transition: color 300ms;
		@media (max-width: 1199px){
			margin-right: 5px;
		}
		@nest &:hover{
			color: #929292;
			@nest & img{
				-webkit-filter: brightness(50%);
			}
		}
		@nest & img{
			position: absolute;
			left: 0;
			top: 50%;
			transition: filter 300ms;
			transform: translateY(-50%);
		}
	}
	@nest &__call{
		font-size: 16px;
		position: relative;
		padding-left: 18px;
		margin-left: 21px;
		color: var(--purple);
		text-decoration: none;
		transition: color 300ms;
		display: inline-block;
		@media (max-width: 1199px){
			display: none;
		}
		@nest &:hover{
			color: #9e30a0;
			@nest & svg{
				fill: #9e30a0;
			}
		}
		@nest & svg{
			position: absolute;
			left: 0;
			top: 50%;
			fill: var(--purple);
			transform: translateY(-50%);
			transition: fill 300ms;
		}
	}
}