.footer-list{
	padding: 0;
	margin: 0;
	list-style: none;
	display: inline-block;
	@nest &__item{
		padding: 0 28px;
		display: inline-block;
		position: relative;
		@media (max-width: 1199px){
			padding: 0 8px;
			@nest &:first-child{
				padding-left: 0;
			}
			@nest &:last-child{
				padding-right: 0;
			}
			@media (max-width: 512px){
				padding: 0;
				display: block;
				margin-bottom: 8px;
			}
		}
		@nest &:after{
			position: absolute;
			content: '';
			right: -2px;
			top: 50%;
			width: 1px;
			height: 17px;
			margin-top: -8.5px;
			background: #dedede;
			@media (max-width: 512px){
				display: none;
			}
		}
		@nest &:last-child{
			@nest &:after{
				display: none;
			}
		}
	}
	@nest &__link{
		font-size: 18px;
		color: #000;
		font-weight: 300;
		text-decoration: none;
		transition: color 300ms;
		@media (max-width: 1199px){
			font-size: 16px;
			letter-spacing: -0.035px;
		}
		@nest &:hover{
			color: var(--purple);
		}
		@media (max-width: 512px){
			font-size: 20px;
		}
	}
}