.btn-nav{
	border: 0;
	outline: none;
	width: 28px;
	min-height: 19px;
	padding: 0;
	z-index: 10;
	margin: 3px 40px 0 0;
	position: relative;
	vertical-align: middle;
	background-color: transparent;
	@media (max-width: 1199px){
		margin-right: 25px;
	}
	@media (max-width: 991px){
		margin: 0 15px 0 0;
	}
	@media (max-width: 767px){
		float: right;
		margin: 8px 0 0;
	}
	@nest &:hover &{
		@nest &__line{
			background: var(--purple);
		}
	}
	@nest &__line{
		width: 100%;
		height: 3px;
		display: block;
		background: #555;
		margin-bottom: 5px;
		transition: all 300ms;
		@nest &:last-child{
			margin-bottom: 0;
		}
	}
	@nest &-open{
		@nest & .btn-nav__line{
			background: var(--purple);
			@nest &:first-child{
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				margin: auto;
				transform: rotate(-45deg);
			}
			@nest &:nth-child(2){
				opacity: 0;
			}
			@nest &:nth-child(3){
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				margin: auto;
				transform: rotate(45deg);
			}
		}
	}
}