.burger-dropdown{
	margin: 0;
	display: none;
	list-style: none;
	margin-top: 17px;
	padding: 0 5px 0 89px;
	@media (max-width: 512px){
		padding: 0 5px 0 53px;
		margin-top: 11px;
	}
	@nest &__link{
		font-size: 27px;
		color: #8c8c8c;
		display: block;
		line-height: normal;
		margin-bottom: 25px;
		text-decoration: none;
		@media (max-width: 512px){
			font-size: 16px;
			margin-bottom: 17px;
		}
	}
}