.repair-block{
	font-size: 0;
	margin: 25px 0 13px;
	@media (max-width: 991px){
		margin-bottom: 10px;
	}
	@media (max-width: 512px){
		margin: 20px 0 35px;
		text-align: center;
	}
	@nest &:last-child{
		@media (max-width: 512px){
			margin-bottom: 0;
		}
	}
	@nest &__wrap-image{
		height: 130px;
		display: table-cell;
		vertical-align: bottom;
		@media (max-width: 512px){
			height: auto;
			display: block;
		}
	}
	@nest &__link{
		font-size: 18px;
		color: #939393;
		display: block;
		max-width: 185px;
		margin-bottom: 3px;
		line-height: 18.5px;
		text-decoration: none;
		transition: color 300ms;
		@nest &:hover{
			color: #000;
		}
		@media (max-width: 991px){
			margin-bottom: 12px;
		}
		@media (max-width: 512px){
			font-size: 24px;
			max-width: none;
			margin-bottom: 16px;
			color: var(--purple);
		}
		@nest &--title{
			font-size: 24px;
			color: var(--purple);
			margin: 11px 0 7px;
			line-height: normal;
			@nest &:hover{
				color: #9e30a0;
			}
			@media (max-width: 512px){
				font-size: 30px;
				color: #9e30a0;
				margin: 10px 0 13px;
			}
		}
	}
}