.tabs{
	@nest &-list{
		padding: 0;
		list-style: none;
		margin: 0 0 20px;
		border-bottom: 1px solid #ddd;
		@media (max-width: 512px){
			border-bottom: 0;
		}
		@nest &__item{
			margin-right: 42px;
			display: inline-block;
			vertical-align: middle;
			@media (max-width: 767px){
				margin-right: 25px;
			}
			@media (max-width: 512px){
				margin: 0;
				padding-left: 4px;
				display: block;
				border-top: 1px solid #ddd;
			}
			@nest &:last-child{
				@media (max-width: 512px){
					border-bottom: 1px solid #ddd;
				}
			}
			@nest & button{
				font-size: 24px;
				color: #000;
				border: 0;
				outline: none;
				font-weight: 300;
				padding: 0 0 11px;
				position: relative;
				background: transparent;
				transition: color 300ms;
				@nest &:hover{
					color: var(--purple);
				}
				@nest &:before{
					position: absolute;
					content: '';
					left: 0;
					right: 0;
					bottom: -1px;
					width: 0;
					height: 2px;
					background: var(--purple);
					transition: width 300ms;
					@media (max-width: 512px){
						display: none;
					}
				}
				@nest &:after{
					position: absolute;
					content: '';
					top: 50%;
					right: 0;
					margin-top: -3.5px;
					background: resolve('arrow.png');
					width: width('arrow.png');
					height: height('arrow.png');
					@media (min-width: 513px){
						display: none;
					}
				}
				@media (max-width: 512px){
					width: 100%;
					padding: 0;
					text-align: left;
					line-height: 57px;
					color: var(--purple);
				}
			}
			@nest &_active{
				@nest & button{
					color: var(--purple);
					@nest &:before{
						width: 100%;
					}
				}
			}
		}
	}
	@nest &__item{
		@media (max-width: 512px){
			display: none;
			margin-top: 4px;
			padding-bottom: 20px;
		}
		@nest &-link{
			font-size: 18px;
			color: var(--purple);
			display: block;
			min-height: 47px;
			line-height: 45px;
			text-decoration: none;
			border-radius: 10px;
			text-align: center;
			transition: color 300ms, border-color 300ms;
			border: 1px solid var(--purple);
			@nest &:hover{
				color: #9e30a0;
				border-color: #9e30a0;
			}
		}
	}
}