.header-middle{
	padding: 30px 0 29px;
	@media (max-width: 991px){
		padding: 21px 0 18px;
	}
	@media (max-width: 512px){
		margin-top: 70px;
		padding: 11px 0 10px;
	}
	@nest &__content{
		font-size: 0;
		margin: 0 -15px;
		@media (max-width: 1199px){
			margin: 0;
			text-align: center;
		}
	}
	@nest &__item{
		width: 25%;
		padding: 0 15px;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 1199px){
			width: auto;
		}
		@media (max-width: 991px){
			padding: 0;
			width: auto;
			margin: 0;
		}
	}
	@nest &__logo{
		transform: scale(1);
		display: inline-block;
		transition: transform 300ms;
		@nest &:hover{
			transform: scale(.97);
		}
		@media (max-width: 512px){
			margin-top: 3px;
		}
		@nest & img{
			@media (max-width: 991px){
				height: 52px;
			}
			@media (max-width: 512px){
				height: 37px;
			}
		}
	}
	@nest &__time{
		margin: 12px 0 0;
		padding-left: 32px;
		@media (max-width: 1199px){
			padding-left: 15px;
			margin-bottom: 30px;
		}
		@media (max-width: 991px){
			padding: 0;
			margin: 0;
		}
		@nest &--indent-sides{
			padding-left: 82px;
			@media (max-width: 1199px){
				padding-left: 15px;
			}
			@media (max-width: 991px){
				display: none;
			}
		}
		@nest & img{
			margin-right: 11px;
			display: inline-block;
			vertical-align: middle;
		}
		@nest & span{
			font-size: 16px;
			color: #000;
			margin-top: 2px;
			text-align: left;
			line-height: normal;
			display: inline-block;
			vertical-align: middle;
			@nest & a{
				display: block;
				text-decoration: none;
				transition: color 300ms;
				@nest &:hover{
					color: var(--purple);
				}
			}
		}
	}
	@nest &__number{
		padding-left: 56px;
		@media (max-width: 1199px){
			padding-left: 15px;
		}
		@media (max-width: 991px){
			padding: 0;
			margin-top: 25px;
			display: block;
			text-align: center;
		}
		@media (max-width: 512px){
			margin-top: 17px;
			padding-left: 23px;
			text-align: left;
		}
		@nest & a{
			font-size: 24px;
			color: #000;
			display: block;
			line-height: normal;
			text-decoration: none;
			margin-bottom: 6px;
			@media (max-width: 991px){
				font-size: 34px;
				margin-bottom: 11px;
			}
			@media (max-width: 512px){
				font-size: 22px;
				margin-bottom: 7px;
			}
		}
	}
	@nest &__basket{
		float: right;
		width: 24px;
		height: 32px;
		margin-top: 3px;
		position: relative;
		@media (max-width: 1199px){
			float: none;
			margin-left: 15px;
			display: inline-block;
			vertical-align: top;
		}
		@media (max-width: 991px){
			float: right;
			width: auto;
			height: auto;
			margin: 0;
		}
		@nest &:hover{
			@nest & svg{
				fill: var(--purple);
			}
		}
		@nest & svg{
			transition: fill 300ms;
			@media (max-width: 991px){
				height: 36px;
				width: 27px;
			}
		}
		@nest & span{
			position: absolute;
			content: '';
			font-size: 12px;
			color: var(--purple);
			display: block;
			left: 50%;
			bottom: 4px;
			max-width: 19px;
			overflow: hidden;
			white-space: nowrap;
			-ms-text-overflow: ellipsis;
			text-overflow: ellipsis;
			transform: translateX(-50%);
			@media (max-width: 991px){
				font-size: 13.5px;
				bottom: 9px;
			}
		}
	}
}