.select{
	width: 100%;
	position: relative;
	max-width: 197px;
	display: inline-block;
	vertical-align: middle;
	@nest &:after{
		position: absolute;
		content: '';
		right: 12px;
		top: 50%;
		border-right: 4px solid transparent;
		border-left: 4px solid transparent;
		border-top: 8px solid #000;
		transform: translateY(-50%);
	}
	@nest & select{
		font-size: 14px;
		color: #000;
		height: 33px;
		width: 100%;
		outline: none;
		cursor: pointer;
		border-radius: 5px;
		padding-left: 17px;
		-webkit-appearance: none;
		-ms-appearance: none;
		-moz-appearance: none;
		border: 1px solid #f0f0f0;
		/* background-image: -moz-linear-gradient( 90deg, rgb(229,229,229) 0%, rgb(254,254,254) 100%); */
		/* background-image: -webkit-linear-gradient( 90deg, rgb(229,229,229) 0%, rgb(254,254,254) 100%); */
		/* background-image: -ms-linear-gradient( 90deg, rgb(229,229,229) 0%, rgb(254,254,254) 100%); */
		box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.15);
	}
}