.delivery{
	background: #f4f4f4;
	border-radius: 10px;
	padding: 21px 35px 26px 29px;
	@media (max-width: 991px){
		padding: 21px 35px 17px 36px;
	}
	@media (max-width: 512px){
		border-radius: 0;
		padding: 26px 0 16px;
		text-align: center;
	}
	@nest &__left{
		padding-right: 0;
		@media (max-width: 991px){
			padding-right: 15px;
		}
	}
	@nest & h3{
		font-size: 18px;
		color: #000;
		margin: 0;
		font-weight: 500;
		line-height: normal;
		@media (max-width: 512px){
			font-size: 22px;
		}
	}
	@nest & h4{
		font-size: 18px;
		color: #000;
		font-weight: 500;
		margin: 1px 0 11px;
		line-height: normal;
		@media (max-width: 512px){
			margin: 12px 0 15px;
		}
		@nest & a{
			color: #000;
			text-decoration: none;
			@media (max-width: 512px){
				font-size: 22px;
				display: block;
				margin-top: 3px;
			}
		}
	}
	@nest & span{
		font-size: 14px;
		color: #000;
		font-weight: 100;
		display: block;
		line-height: 17px;
		padding-left: 20px;
		margin: 1px 0 11px;
		@media (max-width: 991px){
			padding-left: 8px;
		}
		@media (max-width: 512px){
			margin: 0 0 11px;
			padding-left: 0;
		}
		@nest &:first-child{
			@nest & br{
				@media (max-width: 512px){
					display: block;
				}
			}
		}
		@nest & br{
			@media (max-width: 991px){
				display: none;
			}
		}
	}
	@nest & .btn-modal{
		@media (max-width: 991px){
			font-size: 20px;
		}
		@media (max-width: 512px){
			margin-bottom: 16px;
		}
	}
}