.nav-list{
	margin: 0;
	font-size: 0;
	padding: 0 23px;
	list-style: none;
	@media (max-width: 991px){
		padding: 0 15px;
	}
	@media (max-width: 512px){
		padding: 0;
	}
	@nest &__item{
		width: 20%;
		position: relative;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 512px){
			width: 100%;
			display: block;
			text-align: left;
		}
		@nest &:hover{
			@media (min-width: 513px){
				@nest & > a{
					color: var(--purple);
					background: #fafafa;
					@nest & svg{
						fill: var(--purple);
					}
				}
				@nest & .nav-hidden{
					opacity: 1;
					height: auto;
					z-index: 100;
				}
			}
		}
		@nest &--dropdown{
			@nest &:hover{
				box-shadow: 0px 6px 25px 0px rgba(213, 80, 208, 0.4);
				@media (max-width: 512px){
					box-shadow: none;
				}
			}
		}
		@nest &:last-child{
			@nest & .nav-list__link{
				@nest &:after{
					display: none;
				}
			}
		}
	}
	@nest &__link{
		font-size: 15px;
		color: #010001;
		display: block;
		z-index: 105;
		position: relative;
		padding: 24px 0 18px;
		text-decoration: none;
		transition: color 300ms;
		@media (max-width: 991px){
			padding: 21px 17px 13px;
			line-height: 19px;
		}
		@media (max-width: 512px){
			font-size: 18px;
			padding: 0;
			display: block;
			line-height: 62px;
			@nest &:after{
				position: absolute;
				content: '';
				left: -15px;
				right: -15px;
				bottom: 0;
				height: 1px;
				background: #ddd;
			}
		}
		@nest &.active{
			color: var(--purple);
			@nest & svg{
				fill: var(--purple);
				@nest &.nav-list__arrow{
					transform: translateY(-50%) rotate(180deg);
				}
			}
		}
		@nest & svg{
			@nest &:first-child{
				display: block;
				margin: 0 auto 7px;
				transition: fill 300ms;
				@media (max-width: 991px){
					height: 40px;
					margin-bottom: 8px;
				}
				@media (max-width: 512px){
					height: 42px;
					max-width: 51px;
					margin: 10px 10px 0 0;
					display: inline-block;
					vertical-align: top;
				}
			}
		}
		@nest & span{
			@media (max-width: 991px){
				display: block;
			}
			@media (max-width: 512px){
				display: inline-block;
			}
		}
		@nest & i{
			position: absolute;
			content: '';
			top: 50%;
			right: 0;
			transform: translateY(-50%);
		}
	}
	@nest &__arrow{
		position: absolute;
		content: '';
		right: 0;
		top: 50%;
		transform: translateY(-50%);
	}
}