.login{
  padding: 0 0 17px 18px;
  border-bottom: 2px solid #e3e3e3;
  @media (max-width: 512px){
    padding-bottom: 9px;
    border-width: 1px;
  }
  @nest & img{
    height: 50px;
    margin: 0 0 9px 3px;
    @media (max-width: 512px){
      height: 32px;
      margin-bottom: 2px;
    }
  }
  @nest &__link{
    font-size: 31px;
    color: #000;
    display: block;
    margin-bottom: 5px;
    line-height: 35px;
    text-decoration: none;
    @media (max-width: 512px){
      font-size: 20px;
      line-height: normal;
    }
    @nest &--color{
      color: var(--purple);
    }
  }
}