@import "normalize-css/normalize.css";
@import "slick-carousel/slick/slick.css";
@import "fancybox/source/jquery.fancybox.css";
/*! main */
@font-face {
  font-family: 'museo_sans';
  src: url('../fonts/museosanscyrl.woff2') format('woff2'),
       url('../fonts/museosanscyrl.woff') format('woff'),
       url('../fonts/museosanscyrl.ttf') format('truetype'),
       url('../fonts/museosanscyrl.svg#museo_sans_cyrl100') format('svg');
  font-weight: 100;
  font-style: normal;
}
@font-face {
  font-family: 'museo_sans';
  src: url('../fonts/museosanscyrl_0.woff2') format('woff2'),
       url('../fonts/museosanscyrl_0.woff') format('woff'),
       url('../fonts/museosanscyrl_0.ttf') format('truetype'),
       url('../fonts/museosanscyrl_0.svg#museo_sans_cyrl300') format('svg');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'museo_sans';
  src: url('../fonts/museosanscyrl_1.woff2') format('woff2'),
       url('../fonts/museosanscyrl_1.woff') format('woff'),
       url('../fonts/museosanscyrl_1.ttf') format('truetype'),
       url('../fonts/museosanscyrl_1.svg#museo_sans_cyrl500') format('svg');
  font-weight: 500;
  font-style: normal;
}

:root{
  --purple: #d550d0;
}

html, body{
  font-family: 'museo_sans';
  font-weight: 300;
  overflow-x: hidden;
}

body{
  @nest &:after{
    position: fixed;
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: -1;
    transition: opacity 300ms;
    background: color(#fff alpha(90%));
  }
  @nest &.white-overlay{
    @nest &:after{
      opacity: 1;
      z-index: 150;
      @media (min-width: 992px){
        opacity: 0;
        z-index: -1;
      }
    }
  }
}

.mobile{
  @media (max-width: 512px){
    margin: 0 -15px !important;
  }
}
.jqselect{
  width: 100%;
}