.news-list{
	padding: 0;
	margin: 0;
	list-style: none;
	padding: 20px 17px 0 25px;
	@media (max-width: 512px){
		padding-top: 26px;
	}
	@nest &__item{
		display: block;
		cursor: pointer;
		border-bottom: 2px solid #4a4a4a;
		@media (max-width: 512px){
			margin-bottom: 7px;
		}
		@nest &:last-child{
			border-bottom: 0;
		}
	}
	@nest &__link{
		font-size: 30px;
		color: #fff;
		padding-left: 20px;
		line-height: normal;
		text-decoration: none;
		letter-spacing: .4px;
		display: inline-block;
		vertical-align: middle;
		padding: 29px 17px 28px 19px;
		@media (max-width: 512px){
			font-size: 18px;
			padding: 10px 0px 15px 10px;
		}
	}
}
.nicescroll-rails{
	clear: fix;
}