.btn-settings{
	padding: 0;
	border: 0;
	float: right;
	outline: none;
	background: transparent;
	transition: fill 300ms;
	@media (max-width: 991px){
		margin: 3px 68px 0 0;
	}
	@media (max-width: 512px){
		margin: 3px 48px 0 0;
	}
	@nest & svg{
		transition: fill 300ms;
	}
	@nest &:hover{
		@nest & svg{
			fill: var(--purple);
		}
	}
}