.footer{
	padding: 16px 0 50px;
	background: #f4f4f4;
	@media (max-width: 1199px){
		padding-top: 16px;
	}
	@media (max-width:991px){
		padding-bottom: 11px;
	}
	@nest &__bottom{
		text-align: center;
		padding: 12px 0 9px;
		@media (max-width: 512px){
			padding-top: 19px;
		}
		@nest & p{
			font-size: 12px;
			color: #8c8c8c;
			margin-bottom: 3px;
			line-height: normal;
			@media (max-width: 512px){
				margin-bottom: 15px;
			}
		}
		@nest & span{
			font-size: 12px;
			color: #000;
			margin: 0 5px;
			line-height: normal;
			display: inline-block;
			@media (max-width: 512px){
				display: block;
			}
			@nest & a{
				color: #000;
				text-decoration: none;
				@media (max-width: 512px){
					display: block;
					font-size: 18px;
					margin: 6px 0 14px;
				}
			}
		}
	}
}