.header-top{
	padding: 8px 0 9px;
	background: #f5f5f5;
	@media (max-width: 991px){
		padding: 17px 0 11px;
	}
	@media (max-width: 512px){
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		z-index: 150;
	}
	@nest &__city{
		@nest & span{
			font-size: 14px;
			color: #000;
			font-weight: 100;
			margin-right: 5px;
			display: inline-block;
			vertical-align: middle;
			@media (max-width: 991px){
				display: block;
				margin: 0;
			}
			@media (max-width: 512px){
				font-size: 14px;
			}
		}
	}
	@nest &__location{
		margin-top: 2px;
		text-align: right;
		@media (max-width: 991px){
			display: none;
		}
		@nest & span{
			font-size: 14px;
			margin: 0 2px 0 0;
			vertical-align: middle;
		}
		@nest & a{
			font-size: 14px;
			color: var(--purple);
			display: inline-block;
			vertical-align: middle;
			@nest &:hover{
				text-decoration: none;
			}
		}
	}
}

.close-link{
	position: absolute;
	content: '';
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: none;
	@nest &.active{
		display: block;
		z-index: 500;
	}
}