.btn-menu{
	border: 0;
	padding: 0;
	outline: none;
	width: 31px;
	height: 27px;
	float: left;
	margin: 5px 0 0 1px;
	position: relative;
	background-color: transparent;
	@media (min-width: 992px){
		display: none;
	}
	@nest &__line{
		display: block;
		width: 100%;
		height: 3px;
		background: #000;
		margin-bottom: 9px;
		-webkit-transition: all 300ms;
		transition: all 300ms;
		@nest &:last-child{
			margin-bottom: 0;
		}
	}
	@nest &-open{
		@nest & .btn-menu__line{
			@nest &:first-child{
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				margin: auto;
				transform: rotate(-45deg);
			}
			@nest &:nth-child(2){
				display: none;
			}
			@nest &:nth-child(3){
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				margin: auto;
				transform: rotate(45deg);
			}
		}
	}
}