.fixed-submenu{
	position: absolute;
	content: '';
	top: 100%;
	left: 0;
	right: 0;
	min-height: 73px;
	text-align: center;
	transition: all 300ms;
	transform-style: preserve-3d;
	transform-origin: 50% 0;
	background: color(#000 alpha(70%));
	transform: perspective(900px) rotateX(-90deg);
	@nest &-open{
		transform: perspective(900px) rotateX(0);
	}
	@nest &__list{
		margin: 0;
		list-style: none;
		padding: 26px 0 25px;
	}
	@nest &__item{
		margin: 0 22px;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 991px){
			margin: 5px 15px;
		}
	}
	@nest &__link{
		font-size: 16px;
		color: #fff;
		text-decoration: none;
		transition: color 300ms;
		@nest &:hover{
			color: var(--purple);
		}
	}
}