.additional-menu{
	position: absolute;
	content: '';
	top: 0;
	right: 8px;
	width: 100%;
	z-index: 200;
	max-width: 385px;
	background: #fafafa;
	transform: translateX(300%);
	padding: 22px 23px 28px 14px;
	transition: all 300ms;
	box-shadow: 0px 5px 15px 0px rgba(0, 0, 0, 0.15);
	@media (max-width: 512px){
		position: fixed;
		right: 0;
		max-width: 251px;
		padding: 15px 23px 18px 4px;
	}
	@nest &.hide{
		transform: translateX(300%);
	}
	@nest &.show{
		transform: translateX(0);
	}
	@nest & .header-top__city{
		padding: 24px 0 32px 18px;
		border-bottom: 2px solid #e3e3e3;
		@media (max-width: 512px){
			border-width: 1px;
			padding: 16px 0 20px 18px;
		}
		@nest & span{
			font-size: 21px;
			margin-bottom: 8px;
			@media (max-width: 512px){
				font-size: 14px;
				margin-bottom: 5px;
			}
		}
	}
	@nest & .header-top__select{
		max-width: 307px;
		@nest &:after{
			right: 15px;
			border-right: 6px solid transparent;
			border-left: 6px solid transparent;
			border-top: 11px solid #000;
			@media (max-width: 512px){
				right: 16px;
				border-right: 4px solid transparent;
				border-left: 4px solid transparent;
				border-top: 7px solid #000;
			}
		}
		@nest & select{
			height: 48px;
			font-size: 21px;
			padding-left: 29px;
			@media (max-width: 512px){
				font-size: 14px;
				height: 33px;
				padding-left: 18px;
			}
		}
	}
	@nest & .btn-modal{
		font-size: 24px;
		height: 56px;
		width: 100%;
		max-width: 327px;
		margin-left: 20px;
		font-weight: 300;
		@media (max-width: 512px){
			font-size: 16px;
			padding: 0;
			height: 37px;
			margin: 0;
		}
	}
	@nest & .header-middle__time{
		margin: 25px 0 0 19px;
		@media (max-width: 512px){
			margin-top: 15px;
		}
		@nest & img{
			width: 42px;
			margin-right: 10px;
			@media (max-width: 512px){
				width: 27px;
				height: 27px;
				margin-right: 5px;
			}
		}
		@nest & span{
			font-size: 21px;
			margin-top: 1px;
			line-height: 26px;
			@media (max-width: 512px){
				font-size: 14px;
				margin-top: 3px;
				line-height: 16px;
			}
		}
	}
}