.modal-girl{
  width: 100%;
  background: #aaa;
  position: relative;
}
.modal-girl button{
  position: fixed;
  content: '';
  right: 50px;
  bottom: 50px;
  width: 50px;
  height: 50px;
}

.modal-block{
  width: 480px;
  min-height: 450px;
  border-radius: 3px;
  background-color: #fff;
  background: #fff;
  box-shadow: 0 10px 20px 0 rgba(35,50,56,0.3);
  position: fixed;
  right: 20px;
  bottom: 120px;
  z-index: 700;
  -webkit-animation-duration: .5s;
  animation-duration: .5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-transition-timing-function: cubic-bezier(0.86,0,0.07,1);
  transition-timing-function: cubic-bezier(0.86,0,0.07,1);
  opacity: 1;
  display: none;
}
.modal-block.rotated{
  bottom: 140px;
  animation-name: rotateInUpRight;
  opacity: 1;
}
.modal-block.rotatedBack {
  animation-name: rotateOutDownRight;   
  opacity: 0;
}
.modal-block:after{
  position: absolute;
  content: '';
  display: block;
  bottom: -7px;
  right: 40px;
  width: 20px;
  height: 20px;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  z-index: 15;
  background: #fff;
}


.livechat-girl {
  width: 60px;
  height: 60px;
  z-index: 200;
  border-radius: 50%;
  position: fixed;
  bottom: 0;
  right: 40px;
  opacity: 0;
  -webkit-box-shadow: 0 5px 10px 0 rgba(35,50,56,0.3);
  box-shadow: 0 5px 10px 0 rgba(35,50,56,0.3);
  z-index: 700;
  transform: translateY(0);
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  cursor: pointer;
  -webkit-transition: all 1s cubic-bezier(0.86,0,0.07,1);
  transition: all 1s cubic-bezier(0.86,0,0.07,1)
}

.livechat-girl:focus {
  outline: 0
}

.livechat-girl.animated {
  opacity: 1;
  transform: translateY(-40px);
  -webkit-transform: translateY(-40px);
  -ms-transform: translateY(-40px)
}

.livechat-girl:after {
  content: '';
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: linear-gradient( 90deg, rgb(101,31,102) 0%, rgb(173,65,169) 100%);
  background: -ms-linear-gradient( 90deg, rgb(101,31,102) 0%, rgb(173,65,169) 100%);
  position: absolute;
  right: 1px;
  top: 1px;
  z-index: 50
}

.livechat-girl .girl {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  z-index: 50
}

.livechat-girl .animated-circles .circle {
  background: rgba(101,31,102,0.25);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  position: absolute;
  z-index: 49;
  transform: scale(1);
  -webkit-transform: scale(1)
}

.livechat-girl .animated-circles.animated .c-1 {
  animation: 2000ms scaleToggleOne cubic-bezier(0.25,0.46,0.45,0.94) forwards
}

.livechat-girl .animated-circles.animated .c-2 {
  animation: 2500ms scaleToggleTwo cubic-bezier(0.25,0.46,0.45,0.94) forwards
}

.livechat-girl .animated-circles.animated .c-3 {
  animation: 3000ms scaleToggleThree cubic-bezier(0.25,0.46,0.45,0.94) forwards
}

.livechat-girl.animation-stopped .circle {
  opacity: 0!important
}

.livechat-girl .livechat-hint {
  position: absolute;
  right: 40px;
  top: 50%;
  margin-top: -20px;
  opacity: 0;
  z-index: 0;
  -webkit-transition: all .3s cubic-bezier(0.86,0,0.07,1);
  transition: all .3s cubic-bezier(0.86,0,0.07,1)
}

.livechat-girl .livechat-hint.show_hint {
  -webkit-transform: translateX(-40px);
  transform: translateX(-40px);
  opacity: 1
}

.livechat-girl .livechat-hint.hide_hint {
  opacity: 0;
  -webkit-transform: translateX(0);
  transform: translateX(0)
}

.livechat-girl .livechat-hint.rd-notice-tooltip {
  max-width: 1296px!important
}

.livechat-girl .livechat-hint.rd-notice-tooltip .rd-notice-content {
  width: 350px;
  padding: 10px 10px;
  color: #fff;
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.rd-notice-type-success .rd-notice-content {
  min-height: 40px;
  background: resolve('tooltip.svg') no-repeat center;
  background-size: 100%;
}

@media only screen and (max-width: 1599px) {
  .livechat-girl .livechat-hint.rd-notice-tooltip {
    max-width:1060px!important
  }
}

@media only screen and (max-width: 1309px) {
  .livechat-girl .livechat-hint.rd-notice-tooltip {
    max-width:984px!important
  }
}

@media only screen and (max-width: 1124px) {
  .livechat-girl .livechat-hint.rd-notice-tooltip {
    max-width:600px!important
  }
}
@media (max-width: 512px) {
  .livechat-girl{
    display: none;
  }
}



