.wrapper-content{
	margin: 12px 0 0;
	padding-bottom: 50px;
	@media (max-width: 991px){
		padding-bottom: 30px;
	}
	@nest &__left{
		width: 72.5%;
		@media (max-width: 991px){
			width: 100%;
		}
	}
	@nest &__right{
		width: 27.5%;
		@media (max-width: 991px){
			width: 100%;
		}
	}
}