.burger-list{
	padding: 0;
	margin: 5px 0 0;
	list-style: none;
	@nest &__item{
		display: block;
		cursor: pointer;
		border-bottom: 2px solid #4a4a4a;
	}
	@nest &__title{
		font-size: 0;
		position: relative;
		padding: 20px 17px 24px 20px;
		@media (max-width: 512px){
			padding: 0 8px 0 17px;
		}
		@nest &:after{
			position: absolute;
			content: '';
			right: 17px;
			top: 50%;
			margin-top: -3.5px;
			transition: transform 300ms;
			background: resolve('arrow.png');
			width: width('arrow.png');
			height: height('arrow.png');
			@media (max-width: 512px){
				right: 8px;
			}
		}
		@nest & a{
			font-size: 25px;
			color: var(--purple);
			padding-left: 20px;
			line-height: 29px;
			text-decoration: none;
			display: inline-block;
			vertical-align: middle;
			@media (max-width: 512px){
				font-size: 15px;
				padding-left: 5px;
				line-height: 53px;
			}
		}
	}
	@nest &__logo{
		display: inline-block;
		vertical-align: middle;
		@nest & svg{
			fill: var(--purple);
			@media (max-width: 512px){
				max-height: 30px;
				max-width: 35px;
			}
		}
	}
	@nest &__arrow{
		position: absolute;
		content: '';
		right: 17px;
		top: 50%;
		transform: translateY(-50%);
		@nest &.rotate{
			transform: translateY(-50%) rotate(180deg);
		}
		@media (max-width: 512px){
			right: 8px;
		}
		@nest & svg{
			fill: var(--purple);
			@media (max-width: 512px){
				width: 10px;
			}
		}
	}
}
.rotate{
	@nest &:after{
		transition: transform 300ms;
		transform: rotate(180deg);
	}
}