.nav-hidden{
	position: absolute;
	content: '';
	left: 0;
	right: 0;
	margin: 0;
	height: 0;
	opacity: 0;
	padding: 0 25px;
	overflow: hidden;
	list-style: none;
	text-align: center;
	background: #fafafa;
	box-shadow: 0px 6px 25px 0px rgba(213, 80, 208, 0.4);
	@media (max-width: 512px){
		opacity: 1;
		height: auto;
		display: none;
		padding: 0 0 0 45px;
		position: relative;
		box-shadow: none;
		text-align: left;
	}
	@nest &__item{
		display: block;
		margin-bottom: 13px;
		@media (max-width: 512px){
			margin-bottom: 14px;
		}
		@nest &:first-child{
			padding: 18px 0 0;
			border-top: 1px solid #ddd;
			@media (max-width: 512px){
				border-top: 0;
				padding-top: 10px;	
			}
		}
	}
	@nest &__link{
		font-size: 15px;
		color: #000;
		display: block;
		text-decoration: none;
		transition: color 300ms;
		@nest &:hover{
			color: var(--purple);
		}
		@media (max-width: 512px){
			font-size: 19px;
		}
	}
}