.header-middle-mobile{
	@media (min-width: 992px){
		display: none;
	}
	@nest & .header-middle__content{
		float: right;
		margin: 5px 51px 0 0;
	}
	@nest &__time{
		float: right;
		margin: 5px 51px 0 0;
		@media (max-width: 767px){
			display: none;
		}
		@media (max-width: 512px){
			margin: 0;
			display: block;
		}
		@nest & img{
			display: none;
		}
		@nest & span{
			margin: 0;
			color: #939393;
			font-size: 18px;
			font-weight: 100;
			display: block;
			text-align: right;
			line-height: normal;
			@media (max-width: 512px){
				font-size: 9px;
			}
		}
		@nest & a{
			font-size: 16px;
			color: #000;
			display: inline-block;
			text-decoration: none;
		}
	}
	@nest & .btn-modal{
		float: right;
		margin-top: 18px;
		@media (max-width: 512px){
			display: none;
		}
	}
}