.block-title{
	font-size: 24px;
	padding: 13px 0 14px;
	font-weight: 300;
	line-height: normal;
	margin-bottom: 0;
	border-bottom: 1px solid #ddd;
	@media (max-width: 767px){
		margin: 0;
	}
	@media (max-width: 512px){
		font-size: 22px;
		padding-bottom: 10px;
	}
}
.block-title--lg{
	padding: 19px 0;
}