.sidebar-product{
	outline: none;
	margin: 5px 10px 9px;
	background: #f4f4f4;
	position: relative;
	padding: 0 0 19px;
	border-radius: 10px;
	@media (max-width: 991px){
		padding-bottom: 15px;
	}
	@nest &__tooltip{
		position: absolute;
		content: '';
		top: 13px;
		right: 15px;
		width: 43px;
		height: 42px;
		z-index: 50;
		outline: none;
		border-radius: 10px;
		transition: background 300ms;
		@media (max-width: 991px){
			width: 35px;
			height: 35px;
			top: auto;
			bottom: 0;
			right: 0;
		}
		@nest &:hover{
			background: #fff;
			@nest & svg{
				fill: var(--purple);
			}
		}
		@nest & svg{
			position: absolute;
			left: 0;
			right: 0;
			top: 50%;
			margin: -12.5px auto 0;
			transition: fill 300ms;
		}
	}
	@nest &__wrap-image{
		height: 245px;
		margin-bottom: 14px;
		position: relative;
		@media (max-width: 991px){
			height: 220px;
			margin-bottom: 4px;
		}
	}
	@nest & img{
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		margin: auto;
		margin-bottom: 0;
		overflow: hidden;
		border-radius: 10px 10px 0 0;
		@media (max-width: 991px){
			margin-bottom: auto;
		}
		@media (max-width: 767px){
			height: 100%;
		}
	}
	@nest &__text{
		padding: 0 15px;
		@nest & > a{
			font-size: 16px;
			color: #000;
			outline: none;
			display: block;
			font-weight: 300;
			margin-bottom: 10px;
			line-height: 19px;
			text-decoration: none;
			transition: color 300ms;
			@nest &:hover{
				color: var(--purple);
			}
			@media (max-width: 991px){
				margin-bottom: 3px;
				letter-spacing: -.05px;
			}
		}
	}
	@nest &__bottom{
		clear: fix;
		@media (max-width: 991px){
			padding-right: 45px;
			position: relative;
		}
	}
	@nest & span{
		font-size: 24px;
		color: #000;
		float: left;
		font-weight: 500;
		max-width: 160px;
		overflow: hidden;
		white-space: nowrap;
		-ms-text-overflow: ellipsis;
		text-overflow: ellipsis;
		@media (max-width: 1199px){
			float: none;
		}
	}
	@nest & .btn-modal{
		float: right;
		@media (max-width: 1199px){
			float: none;
			width: 100%;
			display: block;
			margin-top: 7px;
		}
	}
}