.advantages{
	padding: 28px 0 0 15px;
	background: #f4f4f4;
	border-radius: 10px;
	text-align: center;
	margin-bottom: 8px;
	@media (max-width: 991px){
		margin-bottom: 3px;
		padding: 34px 0 4px 6px;
	}
	@media (max-width: 512px){
		margin: 0;
		padding: 35px 0;
		border-radius: 0;
	}
	@nest &__item{
		max-width: 200px;
		position: relative;
		margin: 0 26px 29px;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 991px){
			max-width: 190px;
			padding: 0 0 0 25px;
			margin: 0 20px 26px;
		}
		@media (max-width: 512px){
			display: block;
			padding-left: 58px;
			max-width: 244px;
			margin: 0 auto 39px;
		}
		@nest &:last-child{
			@media (max-width: 512px){
				margin-bottom: 0;
			}
		}
		@nest &:nth-child(even){
			@media (max-width: 512px){
				padding-left: 0;
				padding-right: 58px;

				@nest & .advantages__item-logo{
					left: auto;
					right: 0;
				}
			}
		}
		@nest &-logo{
			position: absolute;
			content: '';
			left: 0;
			top: 50%;
			width: 49px;
			height: 49px;
			border-radius: 50%;
			margin-top: -24.5px;
			border: 1px solid var(--purple);
			@media (max-width: 991px){
				width: 45px;
				height: 45px;
			}
			@media (max-width: 512px){
				width: 49px;
				height: 49px;
			}
			@nest & svg{
				position: absolute;
				left: 50%;
				top: 50%;
				fill: var(--purple);
				transform: translate(-50%, -50%);
			}
			@nest & i{
				position: absolute;
				content: '';
				left: 50%;
				top: 50%;
				font-size: 30px;
				display: block;
				line-height: 27px;
				font-style: normal;
				color: var(--purple);
				transform: translate(-50%, -50%);
			}
		}
		@nest & span{
			font-size: 13px;
			color: #000;
			display: block;
			padding-left: 58px;
			text-align: left;
			line-height: 15px;
			margin-top: 2px;
			@media (max-width: 991px){
				font-size: 12px;
				margin-top: 0;
				padding-left: 28px;
			}
			@media (max-width: 512px){
				font-size: 16px;
				padding-left: 0;
				line-height: 20px;
			}
		}
	}
}