.btn-modal{
	font-size: 18px;
	color: #fff;
	border: 0;
	outline: none;
	border-radius: 18px;
	padding: 4px 31px 6px;
	border: 1px solid #b558b3;
	text-shadow: 0px 1px 1px rgba(158, 48, 160, 0.2);
	background-image: -moz-linear-gradient( 90deg, rgb(158,48,160) 0%, rgb(213,80,208) 100%);
	background-image: -webkit-linear-gradient( 90deg, rgb(158,48,160) 0%, rgb(213,80,208) 100%);
	background-image: -ms-linear-gradient( 90deg, rgb(158,48,160) 0%, rgb(213,80,208) 100%);
	box-shadow: 0px 2px 7px 0px rgba(213, 80, 208, 0.6);
	@nest &:hover{
		background-image: -moz-linear-gradient( 90deg, rgb(101,31,102) 0%, rgb(173,65,169) 100%);
		background-image: -webkit-linear-gradient( 90deg, rgb(101,31,102) 0%, rgb(173,65,169) 100%);
		background-image: -ms-linear-gradient( 90deg, rgb(101,31,102) 0%, rgb(173,65,169) 100%);
		box-shadow: 0px 2px 10px 0px rgba(213, 80, 208, 0.9);
	}
	@media (max-width: 991px){
		font-size: 16px;
	}
	@nest &--lg{
		font-size: 20px;
		border-radius: 23px;
		padding: 9px 39px 8px;
		@media (max-width: 991px){
			font-size: 16px;
		}
	}
	@nest &--sm{
		border-radius: 9px;
		padding: 3px 19px 5px;
	}
	@nest &--bold{
		font-weight: 500;
	}
}