.fixed-nav{
	background: #000;
	padding: 6px 0 5px;
	z-index: 110;
	@media (max-width: 991px){
		padding: 14px 0;
	}
	@media (max-width: 512px){
		display: none;
	}
	@nest &.fixed{
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		text-align: right;
		@media (max-width: 767px){
			text-align: left;
		}
		@nest & .fixed-list{
			margin: 3px 4px 0 0;
			@media (max-width: 991px){
				margin: 6px 0 0;
			}
			@nest &__item{
				margin-right: 30px;
				@media (max-width: 991px){
					margin-right: 10px;
				}
				@nest &:last-child{
					@media (max-width: 991px){
						margin-right: 0;
					}
				}
			}
		}
		@nest & .fixed-nav__btn{
			@media (max-width: 1199px){
				display: none;
			}
		}
	}
	@nest &__btn{
		float: right;
		padding-bottom: 5px;
		@media (max-width: 991px){
			display: none;
		}
	}
	@nest &__logo{
		float: left;
	}
}