.footer-middle{
	@nest &__inner{
		padding: 16px 0 18px;
		border-bottom: 1px solid #ddd;
		@media (max-width: 1199px){
			padding: 27px 0 25px;
		}
		@media (max-width: 512px){
			padding: 16px 0 14px;
		}
	}
	@nest &__left{
		max-width: 205px;
		margin-right: 70px;
		display: inline-block;
		vertical-align: top;
		@media (max-width: 1199px){
			margin-right: 40px;
		}
		@media (max-width: 512px){
			display: block;
			margin-right: 0;
			max-width: 100%;
			text-align: center;
		}
		@nest & > a{
			display: block;
			margin-top: 4px;
		}
		@nest & i{
			font-size: 12px;
			color: #010101;
			font-weight: 100;
			display: block;
			margin: 0 0 11px;
			font-style: normal;
			@media (max-width: 512px){
				font-size: 14px;
				margin: 4px 0 7px;
			}
		}
		@nest & span{
			font-size: 13px;
			color: #000;
			display: block;
			@nest & a{
				color: var(--purple);
				text-decoration: none;
			}
		}
	}
	@nest &__nav{
		display: inline-block;
		vertical-align: top;
		@media (max-width: 512px){
			margin-top: 35px;
			position: relative;
		}
		@nest &:before{
			position: absolute;
			content: '';
			top: -17px;
			left: 0;
			right: 0;
			height: 1px;
			display: none;
			background: #ddd;
			@media (max-width: 512px){
				display: block;
			}
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			right: 0;
			height: 1px;
			bottom: -10px;
			display: none;
			background: #ddd;
			@media (max-width: 512px){
				display: block;
			}
		}
		@nest & ul{
			padding: 0;
			list-style: none;
			margin: 0 39px 0 0;
			display: inline-block;
			vertical-align: top;
			@media (max-width: 1199px){
				margin-right: 20px;
			}
			@media (max-width: 512px){
				margin-right: 22px;
				margin-left: 13px;
			}
			@nest & li{
				display: block;
				margin-bottom: 6px;
				@media (max-width: 512px){
					margin-bottom: 10px;
				}
				@nest & a{
					font-size: 16px;
					color: var(--purple);
					text-decoration: none;
					display: inline-block;
					@nest &:hover{
						text-decoration: underline;
					}
				}
			}
		}
	}
	@nest &__info{
		margin-left: 40px;
		display: inline-block;
		vertical-align: top;
		position: relative;
		@media (max-width: 1199px){
			float: right;
			margin-left: 0;
			text-align: right;
		}
		@media (max-width: 512px){
			float: none;
			display: block;
			text-align: center;
			margin-top: 26px;
		}
		@nest &:after{
			position: absolute;
			content: '';
			display: none;
			left: 0;
			right: 0;
			bottom: -17px;
			height: 1px;
			width: 100%;
			background: #ddd;
			@media (max-width: 512px){
				display: block;
			}
		}
		@nest & a{
			font-size: 24px;
			color: #000;
			display: block;
			margin: 1px 0 6px;
			line-height: normal;
			text-decoration: none;
			@media (max-width: 1199px){
				font-size: 21px;
				margin: 2px 0 5px;
			}
			@media (max-width: 512px){
				font-size: 26px;
				margin-bottom: 9px;
			}
		}
		@nest & span{
			font-size: 14px;
			color: #000;
			font-weight: 100;
			display: block;
			line-height: normal;
			margin: 12px 0 0;
			@media (max-width: 1199px){
				font-size: 13px;
				margin: 8px 0 0;
				text-align: left;
				letter-spacing: -0.3px;
			}
			@media (max-width: 512px){
				font-size: 16px;
				margin-top: 13px;
				text-align: center;
			}
		}
		@nest & .btn-modal{
			@media (max-width: 512px){
				font-size: 18px;
				display: block;
				width: 100%;
				max-width: 244px;
				margin: auto;
				height: 42px;
			}
		}
	}
	@nest &__right{
		float: right;
		text-align: right;
		margin-top: 2px;
		@media (max-width: 1199px){
			float: none;
			text-align: center;
			margin-top: 19px;
		}
		@media (max-width: 512px){
			margin-top: 36px;
		}
		@nest & span{
			font-size: 18px;
			color: #000;
			display: block;
			margin-bottom: 6px;
			line-height: normal;
			@media (max-width: 1199px){
				max-width: 120px;
				margin-bottom: 0;
				display: inline-block;
				vertical-align: middle;
			}
			@media (max-width: 512px){
				display: block;
				max-width: 100%;
				margin-bottom: 11px;
			}
		}
		@nest & > a{
			font-size: 0;
			margin-bottom: 6px;
			display: inline-block;
			vertical-align: middle;
			@media (max-width: 1199px){
				margin: 0 21px;
			}
		}
	}
	@nest &__social{
		margin: 9px 0 0;
		@media (max-width: 1199px){
			display: inline-block;
			vertical-align: middle;
		}
		@media (max-width: 512px){
			display: block;
			margin-top: 15px;
		}
		@nest & a{
			width: 36px;
			height: 36px;
			margin-left: 8px;
			border-radius: 50%;
			position: relative;
			border: 1px solid #000;
			display: inline-block;
			transition: border-color 300ms;
			@nest &:hover{
				border-color: var(--purple);
				@nest & svg{
					fill: var(--purple);
				}
			}
			@media (max-width: 512px){
				margin: 0 4px;
			}
			@nest & svg{
				position: absolute;
				top: 50%;
				left: 50%;
				transition: fill 300ms;
				transform: translate(-50%, -50%);
			}
		}
	}
}