.burger-menu{
	position: absolute;
	content: '';
	top: -17px;
	left: -15px;
	width: 100%;
	z-index: 200;
	max-width: 427px;
	background: #000;
	transform: translateX(-300%);
	transition: transform 300ms;
	@media (max-width: 512px){
		position: fixed;
		top: 0;
		left: 0;
		z-index: 999;
		max-width: 260px;
	}
	@nest &.show{
		transform: translateX(0);
	}
}