.btn-close{
	position: absolute;
	content: '';
	right: -60px;
	top: 20px;
	padding: 0;
	border: 0;
	width: 45px;
	height: 36px;
	outline: none;
	background: transparent;
	@media (max-width: 512px){
		right: -60px;
		top: 24px;
		width: 24px;
		right: -32px;
		height: 13px;
	}
	@nest &--left{
		left: -60px;
		@media (max-width: 512px){
			top: 19px;
			left: -38px;
		}
	}
	@nest &__line{
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		width: 45px;
		height: 5px;
		background: #000;
		@media (max-width: 512px){
			width: 30px;
			height: 2px;
		}
		@nest &:first-child{
			transform: rotate(45deg);
		}
		@nest &:last-child{
			transform: rotate(-45deg);
		}
	}
}